

import { DashboardService } from '../dashboard.service';
import { Component, OnInit, Input } from '@angular/core';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
    // import { ToastrManager } from 'ng6-toastr-notifications';
@Component({
  selector: 'app-deletesite',
  templateUrl: './deletesite.component.html',
  styleUrls: ['./deletesite.component.css']
})
export class DeletesiteComponent implements OnInit {

  @Input() siteid;
  constructor(public activeModal: NgbActiveModal,
     private service: DashboardService,
      // private toastService: ToastrManager
      ) { }

  ngOnInit() {
  }
  delete() {
   
    this.service.deletesite(this.siteid).subscribe((response: any) => {
      if (response.statusCode == 200) {
        window.location.reload()
        //  this.toastService.successToastr(response.msg);
      }
      else {
        // this.toastService.errorToastr(response.msg);
      }
      this.activeModal.dismiss();
    })
  }
  close() {
    this.activeModal.dismiss();
  }


}
