import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
// import { ShipManagementServivce } from '../shipmanagement.service'
import { ActivatedRoute, Router } from '@angular/router';
    // import { ToastrManager } from 'ng6-toastr-notifications';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DashboardService } from '../dashboard.service';

@Component({
  selector: 'app-editsite',
  templateUrl: './editsite.component.html',
  styleUrls: ['./editsite.component.css']
})
export class EditsiteComponent implements OnInit {
  structureList: any = ['STRUCTURE1', 'STRUCTURE2', 'STRUCTURE3'];
  selectedOption;
  siteForm: FormGroup;

  loading = false;
  submitted = false;
  @Input() siteid;
  @Input() baseurl;
  @Input() hospital_map;
  @Input() img_map;
  @Input() src_link;
  @Input() stucture_type;
  @Input() title_map;
  @Input() url;
  @Input() url_prefix;


  constructor(
    // private toastService: ToastrManager, 
    private router: Router, 
    public service: DashboardService, 
    public activeModal: NgbActiveModal,
     private _formBuilder: FormBuilder,
      private modalService: NgbModal) { 
        this.selectedOption = this.stucture_type;
      }
      ngOnInit() {
        this.buildSiteForm();
      }
    
      get f() {
        return this.siteForm.controls;
      }
    
     buildSiteForm(){
    
        this.siteForm = this._formBuilder.group({
          URL: ['', [Validators.required]],
          Baseurl: ['', [Validators.required]],
          Titlemap: ['', [Validators.required]],
          Srclink: ['', [Validators.required]],
          Imgmap: [''],
          Hospitalmap: [''],
          Urlprefix: [''],
          Stucturetype: [this.stucture_type, [Validators.required]],
    
        });
    
      }
      editSite(){
        this.submitted = true;
        if (this.siteForm.invalid) {
          return;
        }
        else {
          var addObj = {
              'url': this.siteForm.controls['URL'].value,
              'baseurl': this.siteForm.controls['Baseurl'].value,
              'title_map': this.siteForm.controls['Titlemap'].value,
              'src_link': this.siteForm.controls['Srclink'].value,
              'img_map': this.siteForm.controls['Imgmap'].value,
              'hospital_map': this.siteForm.controls['Hospitalmap'].value,
              'url_prefix': this.siteForm.controls['Urlprefix'].value,
              'stucture_type': this.siteForm.controls['Stucturetype'].value,
          }
        console.log("object add ship =======>", addObj)
          this.activeModal.dismiss();
          this.service.updatesite(this.siteid,addObj).subscribe((response: any) => {
            if (response.status == 200) {
              console.log('AddsiteComponent -> addSite -> response', response)
              window.location.reload()
              // this.toastService.successToastr(response.message);
            }
            else {
              // this.toastService.errorToastr(response.message);
            }
          })
        }
      }
    }
