import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
const URL="http://2001:1600:3:13:f816:3eff:fe02:fc3f:4201"
@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  constructor(private http: HttpClient) { }
  getHeader() {
    let headers = new HttpHeaders()
    headers = headers.set('Content-Type', 'application/json')
    // let token = JSON.parse(localStorage.getItem('_token'))
    // headers = headers.set('Authorization', token)

    return headers;
  }


  getlist() {
    return this.http.get(URL+'/sitelist')
      .pipe((res) => {
        return res;
      });
  }
  addsite(data) {
    return this.http.post(URL+'/addsite', data, { headers: this.getHeader() })
      .pipe((res) => {
        return res;
      });
  }
  deletesite(siteid) {
    return this.http.delete(URL+'/deletesite?id='+siteid, { headers: this.getHeader() })
      .pipe((res) => {
        return res;
      });
  }
  updatesite(siteid,data) {
    return this.http.put(URL+'/updatesite?id='+siteid, data, { headers: this.getHeader() })
      .pipe((res) => {
        return res;
      });
  }
}
